footer {
    background-color: rgba(0, 0, 0, 0.2);
    padding-top: 2em;
    margin-top: 5em;
    display: grid;
}

h1 {
    margin-bottom: 0.5rem;
}

.colormoi {
    box-shadow: 0.3em 0.3em 1em#1b9941, 0 0 0 0.1em#1b9941;
    border-color: 0.1em 0.1em 1em#66CCFF, 0 0 0 0.1em#66CCFF;
}

.mb-3 {
    margin-left: 0.3rem;
}

.mb-3:hover {
    box-shadow: 0.2em 0.2em 0.3em #b3b5b6, 0 0 0 0.1em #acadad;
}

.row g-0 {
    justify-content: center;
    align-items: center;
    padding-inline-start: 1em;
}

.row-py-5 {
    align-items: center;
}

.col {
    width: 100%;
}

@media (min-width: 770px) {
    .col {
        margin-left: auto;
        margin-bottom: 3em;
    }
}

.card {
    width:500px;
    box-shadow: 0.2em 0.2em 0.3em #aeb0b1, 0 0 0 0.1em #bdbebe;
}

.card:hover {
    box-shadow: 0.3em 0.3em 0.6em #7b7d7e, 0 0 0 0.1em #969394;
}

.card-img-top {
    padding: 1em;
}

@media (min-width: 770px) {
    .card {
        display: 100%;
    }
}

.elcentro {
    align-content: center;
    align-items: center;
}

.grid-wrapper {
    background-color: rgba(248, 225, 225, 0.2);
    grid-template-columns: repeat(auto-fit, minmax( 1fr));
    font-weight: 2em;
    margin: 0.5em 1em;
    padding: 0.5em 1em;
    box-shadow: 0.2em 0.2em #dbdcdd, 0 0 0 0.1em #eeeeee;
}

@media (min-width: 770px) {
    .grid-wrapper {
        display: grid;
        grid-template-columns: repeat(3, 1fr)50px repeat(3, 1fr);
        grid-template-rows: 3rem 20px 100px 100px;
        gap: 1rem;
        box-shadow: 0 0 0.2em #dbdcdd, 0.1em #eeeeee;
    }
}

.grid-item {
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0.2em 0.2em 0.3em #e7e9e9, 0 0 0 0.1em #e8ebeb;
    margin-bottom: 20px;
}

.grid-item:hover {
    box-shadow: 0.3em 0.3em 0.6em #7b7d7e, 0 0 0 0.1em #969394;
}

@media (min-width: 770px) {
    .grid-item {
        display: grid;
        text-align: center;
        margin: 10px;
        vertical-align: middle;
        box-shadow: none;
        justify-content: center;
        align-self: center;
    }
}

.heading {
    grid-column-start: 1;
    grid-column-end: 8;
    grid-row-start: 2;
    grid-row-end: 1;
    justify-self: center;
}

img.none {
    object-fit: fill;
}

img.cover {
    object-fit: cover;
}

img.contain {
    object-fit: contain;
}

img.scale-down {
    object-fit: scale-down;
}

.item-1 {
    background: rgb(250, 249, 249);
    justify-self: center;
    text-align: center;
    font-size: 2em;
    height: 100%;
    margin-top: 30px;
    margin-top: 30px;
    padding: 20px;
}

.item-1:hover {
    box-shadow: none;
}

@media (min-width: 770px) {
    .item-1 {
        background: rgb(255, 254, 254);
        grid-area: 1 / 2 /span 2 / span 5;
        width: 100%;
        margin-bottom: 1em;
        margin-top: 30px;
    }
}

.item-2 {
    background: rgb(254, 254, 255);
    img {
        object-fit: fill;
        width: 100%;
        height: 100%;
    }
}

@media (min-width: 770px) {
    .item-2 {
        background: rgb(249, 249, 250);
        grid-area: 5 /3 /span 2 / span 1;
        box-shadow: 0.2em 0.2em 0.3em #aeb0b1, 0 0 0 0.1em #bdbebe;
    }
}

.item-3 {
    background: rgb(252, 253, 252);
    img {
        object-fit: fill;
        width: 100%;
        height: 100%;
    }
}

@media (min-width: 770px) {
    .item-3 {
        background: rgb(249, 252, 250);
        grid-area: 3 / 3 / span 2 / span 1;
        box-shadow: 0.2em 0.2em 0.3em #aeb0b1, 0 0 0 0.1em #bdbebe;
        margin-bottom: 15px;
    }
}

.item-4 {
    background: rgb(250, 250, 248);
    img {
        object-fit: fill;
        width: 100%;
        height: 100%;
    }
}

@media (min-width: 770px) {
    .item-4 {
        background: rgb(252, 252, 249);
        grid-area: 4 / 2 / span 2 / span 1;
        box-shadow: 0.2em 0.2em 0.3em #aeb0b1, 0 0 0 0.1em #bdbebe;
        padding: 1em;
        img {
            object-fit: fill;
            width: 100%;
            height: 100%;
        }
    }
}

.item-5 {
    background: rgb(255, 254, 255);
    font-size: 16px;
    text-align: center;
}

@media (min-width: 770px) {
    .item-5 {
        background: rgb(250, 251, 252);
        grid-area: 5 /3 /span 1 / span 1;
        box-shadow: 0.2em 0.2em 0.3em #aeb0b1, 0 0 0 0.1em #bdbebe;
        font-size: 16px;
        text-align: left;
    }
}

.item-6 {
    background: rgb(255, 254, 255);
    font-size: 16px;
    margin-bottom: 1em;
}

@media (min-width: 770px) {
    .item-6 {
        background: rgb(250, 251, 252);
        grid-area: 3 /2 /span 2 / span 1;
        box-shadow: 0.2em 0.2em 0.3em #aeb0b1, 0 0 0 0.1em #bdbebe;
        font-size: 16px;
        text-align: left;
        align-self: left;
        display: grid;
    }
}

.item-7 {
    background: rgb(255, 254, 255);
    font-size: 16px;
}

@media (min-width: 770px) {
    .item-7 {
        background: rgb(250, 251, 252);
        grid-area: 5 /2 /span 1 / span 1;
        box-shadow: 0.2em 0.2em 0.3em #aeb0b1, 0 0 0 0.1em #bdbebe;
        font-size: 16px;
    }
}

.textblock {
    align-self: center;
}

@media (min-width: 770px) {
    .textblock {
        grid-area: 3 /4 /span 5 / span 3;
        align-self: center;
        margin-left: 20px;
    }
}

.carousel.fade {
    opacity: 1;
}

.carousel.fade .item {
    transition: opacity ease-out .7s;
    left: 1;
    opacity: 1;
    /* hide all slides */
    top: 0;
    position: absolute;
    width: 100%;
    display: block;
}

.carousel.fade .item:first-child {
    top: auto;
    opacity: 1;
    /* show first slide */
    position: relative;
}

.carousel.fade .item.active {
    opacity: 1;
}

.carousel-item {
    opacity: 1;
}